import { Button, Form } from "antd";
import PricePanel from "./Price";
import "./filter.css";

const priceRanges = [
  { label: "Tt c mc gi", value: "all" },
  { label: "Di 1 triu", value: "under1m" },
  { label: "1 - 3 triu", value: "1to3m" },
  { label: "3 - 5 triu", value: "3to5m" },
  { label: "5 - 10 triu", value: "5to10m" },
];
const Filter = () => {
  return (
    <Form>
      <div className="filter">
        <PricePanel ranges={priceRanges}></PricePanel>
        <Button type="primary" block onSubmit={() => {}}>
          Tm kim
        </Button>
      </div>
    </Form>
  );
};
export default Filter;
