import { Menu } from "antd";
import { Header } from "antd/es/layout/layout";
import "./header.css";

const items1 = [
  {
    key: "Trang ch",
    label: "Trang ch",
  },
  {
    key: "Gii thiu",
    label: "Gii thiu",
  },

  {
    key: " Dch v",
    label: " Dch v",
  },

  {
    key: " nh gi",
    label: "nh gi",
  },

  {
    key: "Lin h",
    label: "Lin h",
  },
];

const HeaderC = () => {
  return (
    <Header
      className="header"
      style={{ display: "flex", alignItems: "center" }}
    >
      <Menu
        theme="light"
        mode="horizontal"
        defaultSelectedKeys={["2"]}
        items={items1}
        style={{ flex: 1, minWidth: 0 }}
      />
    </Header>
  );
};
export default HeaderC;
